.Contact{
	background-color: #873e23;
/*	overflow: scroll;*/

}

::-webkit-scrollbar {
  width: 6px;
  height: 0px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
::-webkit-scrollbar-thumb {
  background: #888; 
}

::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.mainC{
	width: 80vw;
	height: 77vh;
	margin-left: 10vw;
	margin-top: 3vh;
	background-color: #F7E0A1 ;
/*	box-shadow: 4px 4px 4px 4px black;*/
	border-radius: 20px;
	text-align: center;
	font-family: monospace;
	box-shadow:rgba(0,0,0,0.8) 0px 5px 15px, inset rgba(0,0,0,0.15) 0px -10px 20px;
/*	overflow: scroll;*/
	
}

.co{
	padding-top: 3vh;
	display: flex;
	flex-direction: column;
	align-items: center;
/*	row-gap: ;*/
}
.contactForm{
	width: 400px;
	height: 350px;
	border: none;
/*	padding-top: 2vh;*/
}

.socialIcons{
	display: flex;
	justify-content: center;
	column-gap: 12px;
}

.socialImg{
	height: 33px;
}

.r{
	color: black;
	text-decoration: none;
}
.c{
	padding-top: 1vh;
}
.point{
	height: 18px;
	width: 22px;
}
