.Home{
	background-color: #873e23;
	

}



.mainH{
	width: 80vw;
	height: 77vh;
	margin-left: 10vw;
	margin-top: 3vh;
	background-color: #F7E0A1 ;
/*	box-shadow: 4px 4px 4px 4px black;*/
	border-radius: 20px;
	text-align: center;
	font-family: monospace;
	box-shadow:rgba(0,0,0,0.8) 0px 5px 15px, inset rgba(0,0,0,0.15) 0px -10px 20px;

	
}

.hd{
	background-color: #10b331;
	border: none;
	border-radius: 5px;
	font-size: 20px;
	width: 100px;
	height: 27px ;
	cursor: pointer;
}
.head{
	position: sticky;
}
.hom{

	font-family: monospace;
	font-size: 21px;
	overflow: scroll;
	height: 65vh;
	text-align: left;
	padding-left: 5vh;
	padding-top: 0vh;
	padding-right: 3vh;
}
.he{
	padding-top: 1vh;
	padding-left: 3vh;
	padding-right: 3vh;
}
@media screen and (max-width: 900px)
{	
	.mainH{
		height: 73vh;
	}
	.hom{
		font-size: 18px;
		overflow: scroll;
		height: 62vh;
		text-align: left;
		padding-left: 3vh;
		padding-top: 0vh;
		padding-right: 3vh;
	}

}
@media screen and (max-width: 450px)
{	
	.mainH{
		height: 73vh;
	}
	.hom{
		font-size: 16px;
		overflow: scroll;
		height: 63vh;
		text-align: left;
		padding-left: 2vh;
		padding-top: 0vh;
		padding-right: 2vh;
/*		padding-bottom: 2vh;*/
	}
	.he{
		font-size: 23px;
	}
}

@media screen and (max-width: 399px)
{
	.Home{
		width: 100vw;
	}
	/*.hom{
		height: 59vh;
	}*/
}