.footer{
	background-color: #eab676;

	display: flex;
	width: 100vw;
	flex-wrap: wrap;
	align-content: center;
	font-family: monospace;
	border: solid 1px black;
}
.center{
	display: flex;
	align-items: center;
	column-gap: 1vh;
}

.left{
	line-height: 0.3;
	align-items: center;
	margin-left: 3vw;
	flex-grow: 1;
}

.right{
	line-height: 0.3;
	align-items: center;
	flex-grow: 1;
}

.f{
	height: 4.5vh;
	cursor: pointer;
	flex-grow: 1;
}

@media only screen and (max-width:800px){
	.left{
		font-size: 9px;
	}
	.right{
		font-size: 9px;
	}
	.f{
		height: 4vh;
	}
}

@media only screen and (max-width:630px){
	.left{
		font-size: 7px;
	}
	.right{
		font-size: 7px;
	}
	.f{
		height: 3vh;
	}
}
@media only screen and (max-width:450px){
	.footer{
		flex-wrap: nowrap;
		flex-direction: column;
	}
	.left{
		font-size: 7.5px;
	}
	.right{
		font-size: 7.5px;
	}
	.f{
		height: 4vh;
	}
	.center{
		margin-left: 30%;
	}
}

@media only screen and (min-width:2500px){
	.left{
		font-size: 23px;
	}
	.right{
		font-size: 23px;
	}
	.f{
		height: 5vh;
	}
}

@media screen and (max-width: 350px)
{
	.Footer{
		width: 100vw;
	}
}