.Gallery{
	background-color: #873e23;
/*	overflow: scroll;*/

}

::-webkit-scrollbar {
  width: 6px;
  height: 0px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
::-webkit-scrollbar-thumb {
  background: #888; 
}

::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.mainG{
	width: 80vw;
	height: 77vh;
	margin-left: 10vw;
	margin-top: 3vh;
	background-color: #F7E0A1 ;
	box-shadow: 4px 4px 4px 4px black;
	border-radius: 20px;
	text-align: center;
	font-family: monospace;
/*	overflow: scroll;*/
	
}
.head{
	position: sticky;
}
.gc{

	font-family: monospace;
	font-size: 21px;
	overflow: scroll;
	height: 65vh;
	text-align: left;
	padding-left: 5vh;
	padding-top: 0vh;
	padding-right: 3vh;
}
.lk{
	color: black;
}
.g{
	padding-top: 1vh;
}
@media screen and (max-width: 900px)
{	
	.mainG{
		height: 73vh;
	}
	.gc{
		font-size: 18px;
		overflow: scroll;
		height: 62vh;
		text-align: left;
		padding-left: 3vh;
		padding-top: 0vh;
		padding-right: 3vh;
	}
}
@media screen and (max-width: 450px)
{	
	.mainG{
		height: 73vh;
	}
	.gc{
		font-size: 16px;
		overflow: scroll;
		height: 63vh;
		text-align: left;
		padding-left: 2vh;
		padding-top: 0vh;
		padding-right: 2vh;
/*		padding-bottom: 2vh;*/
	}
	.g{
		font-size: 23px;
		padding-top: 1vh;
	}
}
@media screen and (max-width: 350px)
{
	.Gallery{
		width: 100vw;
	}
}