.OneA{
	background-color: #873e23;


}

::-webkit-scrollbar {
  width: 10px;
  height: 0px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
::-webkit-scrollbar-thumb {
  background: #888; 
}

::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.mainO{
	width: 80vw;
	height: 77vh;
	margin-left: 10vw;
	margin-top: 3vh;
	background-color: #F7E0A1 ;
	box-shadow: 4px 4px 4px 4px black;
	border-radius: 20px;
	text-align: center;
	font-family: monospace;
/*	overflow: scroll;*/
	
}
.o{
	background-color: #10b331;
	border: none;
	border-radius: 5px;
	font-size: 20px;
	width: 100px;
	height: 35px ;
	cursor: pointer;
/*	margin-left: 30vw;*/

}
.op{
	padding-top: 2vh;
	text-align: center;
}
a{
	color: black;
}

.oa{

	font-family: monospace;
	font-size: 21px;
	overflow: scroll;
	height: 63vh;
	text-align: left;
	padding-left: 5vh;
/*	padding-top: 3vh;*/
	padding-right: 3vh;

}
@media screen and (max-width: 900px)
{	
	.mainO{
		height: 73vh;
	}
	.oa{
		font-size: 18px;
		overflow: scroll;
		height: 60vh;
		text-align: left;
		padding-left: 3vh;
/*		padding-top: 3vh;*/
		padding-right: 3vh;
/*		margin-top: 2vh;*/
	}
	::-webkit-scrollbar {
		  width: 8px;
		  height: 0px;
		}

}
@media screen and (max-width: 450px)
{	
	.mainO{
		height: 73vh;
	}
	.oa{
		font-size: 16px;
		overflow: scroll;
		height: 61vh;
		text-align: left;
		padding-left: 2vh;
/*		padding-top: 3vh;*/
		padding-right: 1vh;
/*		padding-bottom: 2vh;*/
	}
	.h{
		font-size: 23px;
	}
	::-webkit-scrollbar {
		  width: 6px;
		  height: 0px;
		}

}
@media screen and (max-width: 350px)
{
	.OneA{
		width: 100vw;
	}

}