body{
  min-height: 100vh;
}
.App {
  text-align: center;
  height: 100vh;
/*  position: fixed;*/
}



.container{
  display: grid;
  grid-template-rows: 9vh 83vh 9vh;
  width: 100vw;
  position: fixed;
}
.sideB{
  position: relative;
}

@media screen and (max-width: 900px)
{

    .container{
      
      width: 100vw;
      grid-template-rows: 8vh 78vh 15vh;

    }

    ::-webkit-scrollbar {
        width: 10px;
        height: 0px;
      }

      ::-webkit-scrollbar-track {
        background: #f1f1f1; 
      }
       
      /* Handle */
      ::-webkit-scrollbar-thumb {
        background: #888; 
      }

      /* Handle on hover */
      ::-webkit-scrollbar-thumb:hover {
        background: #555; 
      }


}


@media screen and (max-width: 350px)
{
  .App{
    width: 100vw;
  }
}

