.myNav{
  background-color: #eab676;
}
.overlay {
  height: 100%;
  width: 0%;
  position: fixed;
  z-index: 1;
  top: 0;
  right: 0;
  background-color:#3D2B1F;
/*  background-color: rgba(0,0,0, 0.9);*/
  overflow-y: hidden;
  transition: 0.5s;
}

.overlay-content {
  position: relative;
  top: 15%;
  width: 100%;
  text-align: center;
  margin-top: 30px;
}

.overlay a {
  padding: 8px;
  text-decoration: none;
  font-size: 36px;
  color: white;
  display: block;
  transition: 0.3s;
}

.overlay a:hover, .overlay a:focus {
  color: #f1f1f1;
}

.overlay .closebtn {
  position: absolute;
  top: 10px;
  right: 45px;
  font-size: 60px;
}

.nav{
  background-color: #eab676;
  display: flex;
/*  text-decoration: none;*/
}



.l{
  height: 8vh;
  cursor: pointer;
  margin-top: 0.5vh;
}

.s{
  cursor: pointer;
  color: black;
  font-size: 28px;
/*  font-family: Noto Sans;*/
  
}

.div1{
  display: flex;
  margin-left: 2vw;
  column-gap: 0.6vw;
  width: 40vw;
  height: 9vh;
  align-items: center;
/*  margin-top: 5vh;*/

}
.div3{
  display: none;
}
.m{
  height: 5vh;
  margin-top: 4vh;
  cursor: pointer;
}
.div2{
  display: flex;
/*  margin-top: 5vh;*/
  align-items: center;
  column-gap: 2vw;
  width: 55vw;
  font-family: monospace;

}

.n{
  cursor: pointer ;
  color: black;
}


@media screen and (max-width: 900px) {
  .nav{
    align-items: center;
  }

  .s{
    font-size: 25px;
  }
  .div1{
    width: 80vw;
    height: 8vh;
    column-gap: 0.6vw;
/*    margin-top: -1vh;*/
  }
  .l{
    height: 7vh;
  }
  .div2{
    display: none;
  }

.div3{
  display: inline;

}
.m{
  height: 3vh;
  margin-top: 1vh;
}
}

@media screen and (min-width: 2000px){
  .n{
    font-size: 38px;
  }
  .s{
    font-size: 40px;
  }
}

@media screen and (max-width: 900px){
  
    .overlay a{
      font-size: 30px;
    }
}
@media screen and (max-width: 500px){
  .div1{
    column-gap: 0.6vw;
  }
    .s{
      display: none;
    }
    .l{
      height: 7vh;
    }
    .overlay a{
      font-size: 20px;
    }
}
