.Adopt{
	background-color: #873e23;
	overflow: scroll;
	width: 100vw;
}

::-webkit-scrollbar {
  width: 6px;
  height: 0px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
::-webkit-scrollbar-thumb {
  background: #888; 
}

::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.contain{
	display: flex;
	flex-wrap: wrap;
	column-gap: 5vw;
	row-gap: 7vh;
	justify-content: center;
}



.card{
	width: 330px;
	height: 400px;
	border-radius: 20px;
	box-shadow: 4px 4px 4px 4px black;
	background: #F7E0A1;
}
.pic{
	width: 330px;
	height: 300px;
	border-radius: 20px;
}

.b{
	background-color: #10b331;
	border: none;
	border-radius: 5px;
	font-size: 20px;
	width: 300px;
	height: 25px;
	cursor: pointer;
}
.ad{
	font-family: monospace;
}

@media only screen and (max-width: 400px){
	.card{
		width: 280px;
		height: 400px;

	}
	.pic{
		width: 280px;
		height: 300px;
	}
	.b{
		font-size: 20px;
		width: 260px;
		height: 30px;
	}
}

@media only screen and (min-width: 2500px){
	.card{
		width: 600px;
		height: 700px;

	}
	.pic{
		width: 600px;
		height: 500px;
	}
	.b{
		
		border-radius: 10px;
		font-size: 40px;
		width: 560px;
		height: 60px;
		cursor: pointer;
	}
	.ad{
		font-size: 70px;
	}
	.fe{
		font-size: 34px;
	}
}

@media screen and (max-width: 350px)
{
	.Adopt{
		width: 100vw;
	}
}